.profileInfo {
    display: flex;
    background: #edeef0;
    padding-bottom: 10px;
}
.profileInner {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    margin-left: 15px;
    background: #fff;
    padding: 20px;
    width: 100%;
    border-radius: 4px;
}
.profileFooter {
    display: flex;
    justify-content: space-evenly;
    flex-flow: column;
    &__postwrite {
        textarea {
            box-sizing: border-box;
            resize: none;
            width: 100%;
            height: 60px;
            padding: 10px;
        }
    }
    button {
        margin: 0 auto 20px;
    }
    &:hover {
        .backButton {
            opacity: 1;
            visibility: visible;
        }
    }
}
.profileDescr {
    margin-bottom: 15px;
    position: relative;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &::after {
        content: '';
        height: 1px;
        width: 100%;
        position: absolute;
        background: #dce1e6;
        z-index: 1;
        top: 50%;
    }
}
.profileItem {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}
.aboutItem {
    width: 205px;
    cursor: pointer;
    color: #818c99;
    font-size: 13px;
}
.aboutDescr {
    font-size: 13px;
}
/* Кнопка редактировать и кнопка назад */
.editButton, .backButton {
    position: relative;
    z-index: 2;
    background: #fff;
    padding-left: 2px;
    color: #999;
    cursor: pointer;
    transition: 0.1s all ease-in-out;
    opacity: 0;
    visibility: hidden;
    &:hover {
        text-decoration: underline;
    }
}
/* Верх профиля */
.profileHeader {
    &__name {
        font-weight: 600;
    }
    &__status {
        margin: 11px 0 15px;
    }
}
/* Аватарка */
.avatar {
    width: 220px;
    height: 220px;
    margin-bottom: 15px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 4px;
    }
}
.avatarWrap {
    position: relative;
    float: left;
    margin: 0 auto;
    overflow: hidden;
}
.avatarInput {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    opacity: 0;
    cursor: pointer;
    padding-top: 100%;
}
.avatarInner {
    background: #fff;
    border-radius: 4px;
    padding: 15px;
    display: flex;
    flex-flow: column;
    align-items: center;
    height: max-content;
}
.editInfoTitle {
    padding: 0 2px;
    z-index: 2;
    position: relative;
    background: #fff;
    width: max-content;
}
/* Кнопка показать больше инфы */
.moreInfoBtn {
    text-align: center;
    padding: 8px 0 10px;
    margin-bottom: 10px;
    cursor: pointer;
    border-radius: 4px;
    font-size: 13px;
    color: #2a5885;
    &:hover {
        background: #f0f2f5;
    }
}
.upStage {
    &:hover {
        .editButton {
            opacity: 1;
            visibility: visible;
        }
    }
}
.downStage {
    height: 0;
    overflow: hidden;
}
.downStage_active {
    height: 100%;
    overflow: auto;
}