
.post {
	display: flex;
	flex-flow: column;
	padding: 30px 30px 40px;
	position: relative;
	background-color: #fff;
	margin-bottom: 10px;
	border-radius: 4px;
	&:last-child {
		margin-bottom: 0;
	}
}
.head {
	display: flex;
	margin-bottom: 20px;
}
.right {
	display: flex;
	flex-flow: column;
	justify-content: space-between;
	height: calc(100% - 13px);
	a {
		cursor: pointer;
		text-decoration: none;
	}
}
.avatar {
	width: 60px;
	height: 60px;
	margin-right: 25px;
	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
		border-radius: 50%;
	}
}
.name {
	font-weight: 500;
	color: #000;
	width: max-content;
	&:hover {
		border-bottom: 1px solid #000;
	}
}
.link {
	color: #edeef0;
}

.message {
	border-bottom: 1px solid #edeef0;
	padding-bottom: 10px;
}

.footer {
	position: relative;
	padding: 15px 0 0;
}
.likes {
	display: flex;
}
.like {
	width: 20px;
	height: 20px;
	background: transparent;
	border: none;
	display: block;
	position: relative;
	margin-right: 10px;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: url('../Post/icons/heart-trans.svg') center / cover no-repeat;
	}
}